@keyframes move-clock-handles {
  from {
    transform: rotate(0)translateY(-50%);
  }

  to {
    transform: rotate(360deg)translateY(-50%);
  }
}

body {
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  background-color: #fff;
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
}

.stopwatch:after {
  content: "";
  z-index: 10;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minutes-hand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite move-clock-handles;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch__seconds-hand {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite move-clock-handles;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch--speed-up :first-child {
  animation-duration: 600s;
}

.stopwatch--speed-up :nth-child(2) {
  animation-duration: 10s;
}
